:host {
    display: block;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;
}

.startup-package-container {
    width: 100%;
    min-height: 100vh;
    position: relative;
}

$primary-color: #1a1a1a;
$secondary-color: #4a4a4a;
$accent-color: #0066ff;
$accent-hover: #0052cc;
$success-color: #10b981;
$warning-color: #f59e0b;
$startup-color: #8b5cf6;
$startup-hover: #7c3aed;
$text-dark: #0f0f0f;
$text-light: #6b7280;
$white: #ffffff;
$gray-50: #f9fafb;
$gray-100: #f3f4f6;
$gray-200: #e5e7eb;
$gray-300: #d1d5db;
$gray-400: #9ca3af;
$gray-500: #6b7280;
$gray-600: #4b5563;
$gray-700: #374151;
$gray-800: #1f2937;
$gray-900: #111827;

.theme-icon {
    font-size: 1.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: iconPop 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

@keyframes iconPop {
    0% {
        transform: scale(0) rotate(-180deg);
        opacity: 0;
    }
    50% {
        transform: scale(1.2) rotate(10deg);
    }
    100% {
        transform: scale(1) rotate(0deg);
        opacity: 1;
    }
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
}

/* Hero Section */
.hero {
    padding: 8rem 0 6rem;
    background: linear-gradient(135deg, 
        #0f0c29 0%, 
        #302b63 50%, 
        #24243e 100%);
    position: relative;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: 
            radial-gradient(circle at 30% 40%, rgba(99, 102, 241, 0.15) 0%, transparent 50%),
            radial-gradient(circle at 70% 60%, rgba(139, 92, 246, 0.12) 0%, transparent 55%),
            radial-gradient(circle at 50% 80%, rgba(236, 72, 153, 0.1) 0%, transparent 50%);
        opacity: 0.6;
    }
}

.hero-content {
    position: relative;
    z-index: 2;
    text-align: center;
    max-width: 900px;
    margin: 0 auto;
}

.hero-badge {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    background: linear-gradient(135deg, $startup-color, $accent-color);
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 2rem;
    font-size: 0.875rem;
    font-weight: 600;
    margin-bottom: 2rem;
    border: 1.5px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0 4px 15px rgba(139, 92, 246, 0.3);
}

.hero-title {
    font-size: clamp(2.5rem, 6vw, 4rem);
    font-weight: 900;
    line-height: 1.1;
    margin-bottom: 1.5rem;
    color: #ffffff;
    text-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
}

.gradient-text {
    background: linear-gradient(135deg, #8b5cf6 0%, #0066ff 100%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: none;
}

.hero-subtitle {
    font-size: clamp(1.1rem, 2.5vw, 1.25rem);
    color: rgba(255, 255, 255, 0.85);
    margin-bottom: 3rem;
    line-height: 1.6;
    text-shadow: 0 2px 10px rgba(0, 0, 0, 0.2);
}

.hero-actions {
    display: flex;
    gap: 1rem;
    justify-content: center;
    flex-wrap: wrap;
}

.button-primary {
    background: linear-gradient(135deg, $startup-color 0%, $accent-color 100%);
    color: white;
    padding: 1rem 2rem;
    border-radius: 0.75rem;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.1rem;
    transition: all 0.3s ease;
    border: none;
    cursor: pointer;
    min-height: 48px;
    touch-action: manipulation;
    display: inline-flex;
    align-items: center;
    justify-content: center;

    &:hover {
        transform: translateY(-3px);
        box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
    }
}

.button-secondary {
    background: $white;
    color: $text-dark;
    padding: 1rem 2rem;
    border-radius: 0.75rem;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.1rem;
    border: 2px solid $gray-300;
    transition: all 0.3s ease;
    cursor: pointer;
    min-height: 48px;
    touch-action: manipulation;
    display: inline-flex;
    align-items: center;
    justify-content: center;

    &:hover {
        border-color: $startup-color;
        color: $startup-color;
        transform: translateY(-3px);
        box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
    }
}

/* Features Section */
.features {
    padding: 6rem 0;
    background: linear-gradient(135deg, 
        #0f0c29 0%, 
        #302b63 50%, 
        #24243e 100%);
}

.section-header {
    text-align: center;
    margin-bottom: 4rem;
}

.section-title {
    font-size: clamp(2rem, 4vw, 2.5rem);
    font-weight: 800;
    margin-bottom: 1rem;
    color: #ffffff;
    text-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
}

.section-subtitle {
    font-size: 1.125rem;
    color: rgba(255, 255, 255, 0.8);
    max-width: 600px;
    margin: 0 auto;
}

.features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    margin-top: 4rem;
}

.feature-card {
    background: rgba(255, 255, 255, 0.08);
    padding: 2.5rem;
    border-radius: 1rem;
    border: 1px solid rgba(255, 255, 255, 0.15);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    text-align: center;
    backdrop-filter: blur(20px);

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 4px;
        background: linear-gradient(135deg, $startup-color 0%, $accent-color 100%);
        transform: scaleX(0);
        transition: transform 0.3s ease;
    }

    &:hover {
        transform: translateY(-5px);
        box-shadow: 0 20px 40px rgba(139, 92, 246, 0.3);
        border-color: rgba(139, 92, 246, 0.5);
        background: rgba(255, 255, 255, 0.12);

        &::before {
            transform: scaleX(1);
        }
    }
}

.feature-icon {
    font-size: 3rem;
    margin-bottom: 1.5rem;
    display: block;
}

.feature-title {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    color: #ffffff;
}

.feature-description {
    color: rgba(255, 255, 255, 0.75);
    line-height: 1.6;
}

/* Products Section */
.products {
    padding: 6rem 0;
    background: linear-gradient(135deg, 
        #0f0c29 0%, 
        #302b63 50%, 
        #24243e 100%);
}

.products-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2rem;
    margin-top: 4rem;
}

.product-card {
    background: rgba(255, 255, 255, 0.08);
    padding: 2.5rem;
    border-radius: 1rem;
    border: 1px solid rgba(255, 255, 255, 0.15);
    transition: all 0.3s ease;
    backdrop-filter: blur(20px);

    &:hover {
        transform: translateY(-5px);
        box-shadow: 0 20px 40px rgba(139, 92, 246, 0.3);
        background: rgba(255, 255, 255, 0.12);
        border-color: rgba(139, 92, 246, 0.5);
    }
}

.product-icon {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    display: block;
}

.product-title {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    color: #ffffff;
}

.product-description {
    color: rgba(255, 255, 255, 0.75);
    line-height: 1.6;
    margin-bottom: 1.5rem;
}

.product-features {
    list-style: none;
    padding: 0;

    li {
        display: flex;
        align-items: center;
        gap: 0.5rem;
        margin-bottom: 0.5rem;
        color: rgba(255, 255, 255, 0.7);

        &::before {
            content: '✓';
            color: $success-color;
            font-weight: bold;
        }
    }
}

/* Plans Section */
.plans {
    padding: 6rem 0;
    background: linear-gradient(135deg, 
        #0f0c29 0%, 
        #302b63 50%, 
        #24243e 100%);
}

.plans-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2rem;
    margin-top: 4rem;
}

.plan-card {
    background: rgba(255, 255, 255, 0.08);
    border: 2px solid rgba(255, 255, 255, 0.15);
    border-radius: 1.5rem;
    padding: 3rem 2.5rem;
    text-align: center;
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
    backdrop-filter: blur(20px);

    &.featured {
        border-color: $startup-color;
        transform: scale(1.05);
        background: rgba(139, 92, 246, 0.1);

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            height: 6px;
            background: linear-gradient(135deg, $startup-color 0%, $accent-color 100%);
        }
    }

    &:hover {
        transform: translateY(-5px);
        box-shadow: 0 20px 40px rgba(139, 92, 246, 0.3);
        background: rgba(255, 255, 255, 0.12);
    }

    &.featured:hover {
        transform: scale(1.05) translateY(-5px);
    }
}

.plan-badge {
    background: $startup-color;
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 2rem;
    font-size: 0.875rem;
    font-weight: 600;
    margin-bottom: 2rem;
    display: inline-block;
}

.plan-title {
    font-size: 1.8rem;
    font-weight: 800;
    margin-bottom: 1rem;
    color: #ffffff;
}

.plan-price {
    font-size: 2.5rem;
    font-weight: 900;
    color: $startup-color;
    margin-bottom: 0.5rem;
}

.plan-period {
    color: rgba(255, 255, 255, 0.7);
    margin-bottom: 2rem;
}

.plan-features {
    list-style: none;
    text-align: left;
    margin-bottom: 3rem;
    padding: 0;

    li {
        display: flex;
        align-items: center;
        gap: 1rem;
        margin-bottom: 1rem;
        font-size: 1rem;
        color: rgba(255, 255, 255, 0.85);

        &::before {
            content: '✓';
            background: $success-color;
            color: white;
            width: 1.5rem;
            height: 1.5rem;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 0.875rem;
            font-weight: bold;
            flex-shrink: 0;
        }
    }
}

.plan-button {
    background: linear-gradient(135deg, $startup-color 0%, $accent-color 100%);
    color: white;
    padding: 1rem 2rem;
    border-radius: 0.75rem;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.1rem;
    transition: all 0.3s ease;
    width: 100%;
    text-align: center;
    display: block;

    &:hover {
        transform: translateY(-3px);
        box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
    }

    &.secondary {
        background: $white;
        color: $startup-color;
        border: 2px solid $startup-color;

        &:hover {
            background: $startup-color;
            color: white;
        }
    }
}

/* Contact Section */
.contact {
    padding: 6rem 0;
    background: $primary-color;
    color: white;
    text-align: center;

    .section-title {
        color: white;
    }

    .section-subtitle {
        color: $gray-300;
    }
}

.contact-actions {
    display: flex;
    gap: 1rem;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 3rem;
}

.contact-button {
    background: $startup-color;
    color: white;
    padding: 1rem 2rem;
    border-radius: 0.75rem;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.1rem;
    transition: all 0.3s ease;
    min-height: 48px;
    touch-action: manipulation;

    &:hover {
        background: $startup-hover;
        transform: translateY(-3px);
        box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
    }
}

.contact-info {
    margin-top: 2rem;
    color: $gray-300;
}

/* Footer */
.footer {
    background: $gray-900;
    color: $gray-300;
    padding: 2rem 0;
    text-align: center;
}

/* Responsive Design */
@media (max-width: 1024px) {
    .container {
        padding: 0 1.5rem;
    }

    .features-grid,
    .products-grid {
        grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
        gap: 1.5rem;
    }

    .hero {
        padding: 6rem 0 4rem;
    }

    .plan-card.featured {
        transform: none;
    }
}

@media (max-width: 768px) {
    .container {
        padding: 0 1rem;
    }

    .hero {
        padding: 4rem 0 3rem;
    }

    .hero-title {
        font-size: clamp(1.8rem, 8vw, 2.5rem);
        line-height: 1.2;
        margin-bottom: 1rem;
    }

    .hero-subtitle {
        font-size: clamp(1rem, 4vw, 1.1rem);
        margin-bottom: 2rem;
        padding: 0 0.5rem;
    }

    .hero-actions {
        flex-direction: column;
        align-items: center;
        gap: 0.75rem;
    }

    .button-primary,
    .button-secondary {
        width: 100%;
        max-width: 280px;
        text-align: center;
        padding: 1rem 1.5rem;
        font-size: 1.1rem;
    }

    .features-grid,
    .products-grid {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }

    .feature-card,
    .product-card {
        padding: 2rem 1.5rem;
    }

    .plans-grid {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }

    .plan-card {
        padding: 2rem 1.5rem;
    }

    .contact-actions {
        flex-direction: column;
        align-items: center;
        gap: 1rem;
    }

    .contact-button {
        width: 100%;
        max-width: 280px;
        text-align: center;
        padding: 1rem 1.5rem;
        font-size: 1.1rem;
    }

    .section-title {
        font-size: clamp(1.5rem, 6vw, 2rem);
    }

    .section-subtitle {
        font-size: 1rem;
        padding: 0 0.5rem;
    }
}

@media (max-width: 480px) {
    .container {
        padding: 0 0.75rem;
    }

    .hero {
        padding: 3rem 0 2rem;
    }

    .hero-title {
        font-size: clamp(1.5rem, 7vw, 2rem);
        margin-bottom: 0.75rem;
    }

    .hero-subtitle {
        font-size: clamp(0.9rem, 3.5vw, 1rem);
        margin-bottom: 1.5rem;
    }

    .hero-badge {
        font-size: 0.8rem;
        padding: 0.375rem 0.75rem;
        margin-bottom: 1.5rem;
    }

    .features,
    .products,
    .plans,
    .contact {
        padding: 3rem 0;
    }

    .section-title {
        font-size: clamp(1.3rem, 5vw, 1.8rem);
        margin-bottom: 0.75rem;
    }

    .section-subtitle {
        font-size: 0.9rem;
        margin-bottom: 2rem;
    }

    .feature-card,
    .product-card,
    .plan-card {
        padding: 1.25rem;
    }

    .feature-title,
    .product-title {
        font-size: 1.2rem;
        margin-bottom: 0.75rem;
    }

    .feature-description,
    .product-description {
        font-size: 0.9rem;
        margin-bottom: 1.25rem;
    }

    .feature-icon,
    .product-icon {
        font-size: 2rem;
        margin-bottom: 1rem;
    }

    .plan-title {
        font-size: 1.5rem;
    }

    .plan-price {
        font-size: 2rem;
    }

    .plan-features li {
        font-size: 0.9rem;
        margin-bottom: 0.5rem;
    }

    .button-primary,
    .button-secondary,
    .contact-button {
        padding: 0.875rem 1.25rem;
        font-size: 1rem;
        max-width: 260px;
    }
}

/* Animations */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.fade-in {
    opacity: 0;
    transform: translateY(30px);
    transition: all 0.8s ease;

    &.visible {
        opacity: 1;
        transform: translateY(0);
    }
}

/* ================================================
   LIGHT MODE STYLES
   ================================================ */

.light-mode {
    .hero {
        background: linear-gradient(135deg, $gray-50 0%, $white 100%);

        &::before {
            background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="%23e5e7eb" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url(%23grid)"/></svg>');
            opacity: 0.5;
        }
    }

    .hero-title {
        color: $primary-color;
        text-shadow: 0 2px 20px rgba(0, 0, 0, 0.05);
    }

    .hero-subtitle {
        color: $text-light;
        text-shadow: none;
    }

    .hero-badge {
        border-color: rgba(139, 92, 246, 0.3);
        box-shadow: 0 4px 15px rgba(139, 92, 246, 0.2);
    }

    .features,
    .products,
    .plans {
        background: $white;
    }

    .section-title {
        color: $primary-color;
        text-shadow: none;
    }

    .section-subtitle {
        color: $text-light;
    }

    .feature-card,
    .product-card,
    .plan-card {
        background: $white;
        border-color: $gray-200;

        &:hover {
            box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
            border-color: $startup-color;
            background: $white;
        }
    }

    .plan-card.featured {
        background: rgba(139, 92, 246, 0.05);
    }

    .feature-title,
    .product-title,
    .plan-title {
        color: $primary-color;
    }

    .feature-description,
    .product-description {
        color: $text-light;
    }

    .product-features li {
        color: $text-light;
    }

    .plan-period {
        color: $text-light;
    }

    .plan-features li {
        color: $text-dark;
    }

    .button-secondary {
        background: $white;
        color: $text-dark;
        border-color: $gray-300;

        &:hover {
            border-color: $startup-color;
            color: $startup-color;
        }
    }
}

